import React from 'react'
import {  Col, Container, Navbar } from 'react-bootstrap';
import { Link } from 'react-router-dom';

function NavBar({ search }) {
  const handleSearch = (word)=>{
    search(word)
  }
	return (
		<div>
			{" "}
			<Navbar expand="lg" className="bg-body-tertiary">
				<Container>
					<Col xs="3" lg="1">
						<Link to={"/"}>
						
							<img className="logo" src="./images/2.svg" alt="logo" />
						</Link>
					</Col>
					<Col xs="9" lg="11">
						<input type="search" className="form-control" placeholder="" onChange={(e) => handleSearch(e.target.value)} />
					</Col>
				</Container>
			</Navbar>
		</div>
	);
}

export default NavBar