import { Container } from "react-bootstrap";
import NavBar from "./components/NavBar";
import MoviesList from "./components/MoviesList";

import axios from "axios";
import { useEffect, useState } from "react";
import PaginationMovie from "./components/PaginationMovie";
import MovieDetails from "./components/MovieDetails";
import { BrowserRouter, Route, Routes } from "react-router-dom";
function App() {
	const [movie, setMovie] = useState([]);
	const getMovies = async () => {
		const options = {
			method: "GET",
			url: "https://api.themoviedb.org/3/trending/movie/day",
			params: { language: "ar" },
			headers: {
				accept: "application/json",
				Authorization:
					"Bearer eyJhbGciOiJIUzI1NiJ9.eyJhdWQiOiIzNWFlNDk1ZDFmYjdiZGViZjc3YjZjMTM0YjRkYjAzMyIsInN1YiI6IjY1YmMxNDFlZDU1Njk3MDE4M2FhNWU4MCIsInNjb3BlcyI6WyJhcGlfcmVhZCJdLCJ2ZXJzaW9uIjoxfQ.xpgIl0SH3I_eaB_WbwRTW0dgMk5IWsT_KO2InqQdP9g",
			},
		};

		axios
			.request(options)
			.then(function (response) {
				setMovie(response.data.results);
			})
			.catch(function (error) {
				console.error(error);
			});
	};
	const getPage = async (page) => {
		const options = {
			method: "GET",
			url: "https://api.themoviedb.org/3/trending/movie/day",
			params: { language: "ar", page },
			headers: {
				accept: "application/json",
				Authorization:
					"Bearer eyJhbGciOiJIUzI1NiJ9.eyJhdWQiOiIzNWFlNDk1ZDFmYjdiZGViZjc3YjZjMTM0YjRkYjAzMyIsInN1YiI6IjY1YmMxNDFlZDU1Njk3MDE4M2FhNWU4MCIsInNjb3BlcyI6WyJhcGlfcmVhZCJdLCJ2ZXJzaW9uIjoxfQ.xpgIl0SH3I_eaB_WbwRTW0dgMk5IWsT_KO2InqQdP9g",
			},
		};

		await axios
			.request(options)
			.then(function (response) {
				setMovie(response.data.results);
			})
			.catch(function (error) {
				console.error(error);
			});
	};
	const search = async (word) => {
		const options = {
			method: "GET",
			url: `https://api.themoviedb.org/3/search/movie`,
			params: { include_adult: "false", language: "ar", query: word },
			headers: {
				accept: "application/json",
				Authorization:
					"Bearer eyJhbGciOiJIUzI1NiJ9.eyJhdWQiOiIzNWFlNDk1ZDFmYjdiZGViZjc3YjZjMTM0YjRkYjAzMyIsInN1YiI6IjY1YmMxNDFlZDU1Njk3MDE4M2FhNWU4MCIsInNjb3BlcyI6WyJhcGlfcmVhZCJdLCJ2ZXJzaW9uIjoxfQ.xpgIl0SH3I_eaB_WbwRTW0dgMk5IWsT_KO2InqQdP9g",
			},
		};

		await axios
			.request(options)
			.then(function (response) {
				setMovie(response.data.results);
			})
			.catch(function (error) {
				console.error(error);
			});
	};
	useEffect(() => {
		getMovies();
		getPage();
	}, []);
	return (
		<div className="App container">
			<BrowserRouter>
				<NavBar search={search} />
				<Container >
					<Routes>
						<Route path="/" element={<MoviesList movie={movie} getPage={getPage} />} />
						<Route path="/:movieId" element={<MovieDetails />} />
					</Routes>
				</Container>
			</BrowserRouter>
		</div>
	);
}

export default App;
