import React, { useEffect, useState } from "react";
import { Link, useParams } from "react-router-dom";

import axios from "axios";
import { Button, Col, Image, Row } from "react-bootstrap";
function MovieDetails() {
	const [mov, setMov] = useState([]);
	const movieId = useParams();
	const fetchProduct = async () => {
		const options = {
			method: "GET",
			url: `https://api.themoviedb.org/3/movie/${movieId.movieId}`,
			params: { language: "ar" },
			headers: {
				accept: "application/json",
				Authorization:
					"Bearer eyJhbGciOiJIUzI1NiJ9.eyJhdWQiOiIzNWFlNDk1ZDFmYjdiZGViZjc3YjZjMTM0YjRkYjAzMyIsInN1YiI6IjY1YmMxNDFlZDU1Njk3MDE4M2FhNWU4MCIsInNjb3BlcyI6WyJhcGlfcmVhZCJdLCJ2ZXJzaW9uIjoxfQ.xpgIl0SH3I_eaB_WbwRTW0dgMk5IWsT_KO2InqQdP9g",
			},
		};

		await axios
			.request(options)
			.then(function (response) {
				console.log(response.data);
				setMov(response.data);
			})
			.catch(function (error) {
				console.error(error);
			});
	};
	useEffect(() => {
		fetchProduct();
  
	}, []);

	return (
		<>
			<Row className="bg-light">
				<Col sm={8} lg={3}>
					<Image src={`https://image.tmdb.org/t/p/w500/${mov.poster_path}`} thumbnail />
				</Col>
				<Col sm={8} lg={6} className="d-flex justify-content-center align-items-center">
					<div>
						<h2>  : {mov.original_title}</h2>
						<hr />
						<h2>  :{mov.release_date}</h2>
						<hr />

						<h2> : {mov.vote_average}</h2>
						<hr />
						<h2>   :{mov.vote_count}</h2>
					</div>
				</Col>
			</Row>
			<Row className="my-3 bg-light">
				<Col className="p-3">
					<h1>  </h1>
					<h4>{mov.overview}</h4>
				</Col>
			</Row>
			<div className="text-center">
				<Link to={"/"}>
					{" "}
					<Button></Button>
				</Link>
				<a className=" btn btn-dark mx-2 my-5" href={mov.homepage} target="_blank" rel="noreferrer">
					
				</a>
			</div>
		</>
	);
}

export default MovieDetails;
