import { Card, Col } from "react-bootstrap";
import { Link } from "react-router-dom";

function CardMovie({ mov }) {
	return (
		<Col sm="10" md="3" lg={3} className="my-1 cursor-pointer d-flex justify-content-center">
			<Link to={`/${mov.id}`}>
				<Card className="card-head d-flex align-items-center justify-content-center" style={{ width: "18rem" }}>
					<Card.Img variant="top" src={`https://image.tmdb.org/t/p/w500/${mov.poster_path}`} />
					<div className="overlay">
						<p>  : {mov.original_title}</p>
						<p>  :{mov.release_date}</p>
						<p> : {mov.vote_average}</p>
						<p> :{mov.overview.substring(0, 60) + "..."}</p>
					</div>
				</Card>
			</Link>
		</Col>
	);
}

export default CardMovie;
